$perspective:     60rem;
$font-size:       4.25rem;
$split-position:  49%;
$split-thickness: 4px;
$split-color:     #b1b1b1;

.sidebar {
  min-height: 100vh;
  background: $white;
  font-family: $font-1;
  max-width: 270px;
  position: relative;
  display: grid;
  grid-template-columns: 2fr;
  grid-template-rows: 150px 330px 1fr;
  grid-template-areas: 
    "1"
    "2"
    "3"
    "4";

  .id {
    grid-area: 1;
    text-align: center;
    user-select: none;
    position: relative;

    .idContent {
      position: relative;
      margin-top: 10vh;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
      

      img {
        border-radius: 30%;
        width: 25%;
      }
            // === Menu <ul> =====
      .Logo_wrapper {
        line-height: 1;
        text-transform: uppercase;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
      }

      // === Menu item =====
      .Logo {
        position: relative;
        color: transparent;
        cursor: pointer;

        // === Split Line =====
        &::before {
          content: '';
          display: block;
          position: absolute;
          top: $split-position;
          left: -10%;
          right: -10%;
          height: $split-thickness;
          border-radius: $split-thickness;
          margin-top: -($split-thickness / 2);
          background: $split-color;
          transform: scale(0);
          transition: transform .8s cubic-bezier(.16,1.08,.38,.98);
          z-index: 1;
        }
      }

      // === Top/bottom mask =====
      .Mask {
        display: block;
        position: absolute;
        overflow: hidden;
        color: $split-color;
        top: 0;
        height: $split-position;
        transition: all .8s cubic-bezier(.16,1.08,.38,.98);

        span { display: block; }
      }

      // === Bottom mask specific =====
      .Mask + .Mask {
        top: $split-position - 0.1;
        height: 100 - $split-position + 0.1;

        span { transform: translateY(-$split-position); }
      }

      // === Where the fun stuff happens =====
      .Logo:hover,
      .Logo:active {
        .Mask { color: #FFF; transform: skewX(12deg) translateX(5px); }
        .Mask + .Mask { transform: skewX(12deg) translateX(-5px); }
        &::before { transform: scale(0.5); }
      }
      
      h3 {
        font-size: 1.5rem;
        text-transform: uppercase;
        letter-spacing: .2rem;
        margin-top: 1.5rem;
        color: $color-6;
        border: solid 2px $white;
        border-bottom-color: $color-6;
        border-bottom-left-radius: 10%;
        border-bottom-right-radius: 10%;
      }
    }
  }
  .navigation {
    grid-area: 2;
    padding: 2rem 1rem;

    ul {
      li {
        padding: 1rem 0 1rem .4rem;
        font-size: 1.3rem;
        position: relative;

        &:hover i {
          transform: translate(3px, 3px);
        }

        a {
        color: $color-6;
        }
        .navActive {
          color: $color-3;
        }

        i {
          position: absolute;
          font-size: 0.95rem;
          transform: translateY(3px);
          transition: .2s ease;
        }
        span {
          margin-left: 2.7rem;
        }
      }
    }
  }
  .socialNetwork {
    grid-area: 3;
    position: relative;
    *:focus,
    *:active {
      outline: none !important;
      -webkit-tap-highlight-color: transparent;
    }

    .wrapper {
      display: inline-flex;
    }

    .wrapper .icon {
      position: relative;
      background-color: #e9e9e9;
      border-radius: 50%;
      padding: 15px;
      margin: 8px;
      width: 50px;
      height: 50px;
      font-size: 18px;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
      cursor: pointer;
      transition: all 0.2s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    }

    .wrapper .tooltip {
      position: absolute;
      top: 0;
      font-size: 12px;
      background-color: #ffffff;
      color: #ffffff;
      padding: 5px 8px;
      border-radius: 5px;
      box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    }

    .wrapper .tooltip::before {
      position: absolute;
      content: "";
      height: 8px;
      width: 8px;
      background-color: #ffffff;
      bottom: -3px;
      left: 50%;
      transform: translate(-50%) rotate(45deg);
      transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    }

    .wrapper .icon:hover .tooltip {
      top: -40px;
      opacity: 1;
      visibility: visible;
      pointer-events: auto;
    }

    .wrapper .icon:hover span,
    .wrapper .icon:hover .tooltip {
      text-shadow: 0px -1px 0px rgba(0, 0, 0, 0.1);
    }

    .wrapper .facebook:hover,
    .wrapper .facebook:hover .tooltip,
    .wrapper .facebook:hover .tooltip::before {
      background-color: #3b5999;
      color: #ffffff;
    }

    .wrapper .mail:hover,
    .wrapper .mail:hover .tooltip,
    .wrapper .mail:hover .tooltip::before {
      background-color: #BB001B;
      color: #ffffff;
    }

    .wrapper .github:hover,
    .wrapper .github:hover .tooltip,
    .wrapper .github:hover .tooltip::before {
      background-color: #000000;
      color: #ffffff;
    }

    .wrapper .linkedin:hover,
    .wrapper .linkedin:hover .tooltip,
    .wrapper .linkedin:hover .tooltip::before {
      background-color: #5385c2;
      color: #ffffff;
    }
  }
  .signature {
    text-align: left;
    letter-spacing: 0.1rem;
    font-size: .7rem;
    grid-area: 4;
    img{
      width: 35%;
    }
    p{
      color: $black;
      margin-bottom: 1vh;
      margin-left: 1vh;
      font-size: medium;
    }

    a, a:visited {
      color: $color-5;

      &:hover {
        color: $color-4;
      }
    }
  }
}

@media (max-width: 992px){
  .sidebar {
    display: flex;
    max-height: 10px;
    min-height: 50px !important;
    min-width: 100% !important;
    .Logo, .socialNetwork {
      display: none;
    }

    .id {
      display: flex;
      text-align: unset;
      width: min-content;
      .lang {
        align-items: center;
        display: flex;
      }
    }

    .navigation {
      width: 100%;
      padding: 0;
      margin-top: 0 !important;

      ul {
        display: flex;
        justify-content: space-between;

        span {
          margin-left: 1.5rem !important;
        }
      }
    }
    .signature{
      display: none;
    }
    ul {
      justify-content: space-around !important;

      li {
        padding: 1rem 0 !important;
      }
      i {
        font-size: 1.4rem !important;
        transform: translateY(-3px) !important;
      }
      span {
          display: none;
      }
    }
  }
  .scrolled{
    position: fixed !important;
    top: 0 !important;
    left: 0 !important;
    z-index: 1;
  }
}
